<template>
	<view class="find-order">
		<view>
			<view class="fixed-block fs30">
				<view class="search">
					<view class="warp">
						<image src="../../static/icon/search-bg.png" mode=""></image>
						<input class="fs24" type="text" v-model="searchValue" placeholder="">
						<text class="btn" @click="doSearch"></text>
					</view>
				</view>
				<view class="select-section">
					<view class="flex select-order">
						<view @click='getOrderData' :data-nav="1" class="relative" :class="nav == 1 ? 'selectedNav' : ''"></view>
						<view @click='getOrderData' :data-nav="2" class="relative" :class="nav == 2 ? 'selectedNav' : ''"></view>
					</view>
					<scroll-view scroll-x="true" :scroll-left="scrolLeft" class="status-section find-section">
						<view v-if="nav==1" v-for='(item, index) in navTexts.find' :key="index" @click='getOrderTypeData' :data-type="index">
							<text :class="navSecend == index ? 'selected' : ''">{{item}}</text>
						 </view>
						<view v-if="nav==2" v-for='(item, index) in navTexts.fetch' :key="index" @click='getOrderTypeData' :data-type="index">
							<text :class="navSecend == index ? 'selected' : ''">{{item}}</text>
						 </view>
					</scroll-view>
				</view>
			</view>

			<view class="item-container">
				<!--  -->
				<view class="empty" v-if="orderList.length === 0">
					<image src="/static/icon/no_order.png"></image>
					<view class="tc">
						<text class="c999 fs34"></text>
					</view>
				</view>

				<view v-if="orderList.length > 0">

					<block v-for="(item, index) in orderList" :key="index">

						<view class="order-item">
							<view @click='seeDetail' :data-index="index" :data-id="item.id">
								<view class="flex order-status relative">
									<view style="width: 100%;">
										<view class="c999 fs30" style="width: 100%;">
											<text class="fs24 text-666">{{item.order_sn}} </text>
											<text class="flr text-yellow">{{item.status_label}}</text>
										</view>
									</view>
									<!-- <view v-if="status == 1" class="new">
										<image src="../../images/new-1.png"></image>
									</view> -->
								</view>
								<view class="order-info fs30">
									<view class="order-info-left">

										<view class="flex align-item-start">
											<view class="flex-1">
												<view>
													
													<text class="fs24 text-666">{{item.cname}}</text>
												</view>
												<view class="ellipsis">
													<text class="fs24 text-666">{{item.desc}}</text>
												</view>
												<!-- <view class="ellipsis">
													<text class="fs24 text-666">{{item.limit_time_text}}</text>
												</view> -->
												<view class="ellipsis">
													<text class="fs24 text-666" v-if="item.reference_price">:{{item.reference_price}}</text>
												</view>
											</view>
											<view class="item-right">
												<view>
													<text class="fs24 text-yellow">{{item.find_type_label}}</text>
													<!-- <text class="fs24 text-yellow" v-if="item.find_status == 2"></text>
													<text class="fs24 text-yellow" v-if="item.find_status == 3"></text> -->
												</view>
												<view>
													<text class="fs24 text-yellow">: {{item.fee}}</text>
												</view>
											</view>
											<!-- <view class="order-info-right">
												<image v-if="item.is_prompt == 1" src="../../images/reminder.png"></image>
												<image v-if="item.is_prompt != 1" :src="nav == 1 ? '../../images/find.png': '../../images/send.png'" />
											</view> -->

										</view>
										<!--  -->
										<view v-if="item.desc_img.length>0">
											<view class="order-pics-list">
												<image mode='scaleToFill' @click.stop='preview' v-for="(img, idx) in item.desc_img" :key="idx" :src="img"
												 :data-idx="idx" :data-index="index"></image>
											</view>
										</view>


										<!--  -->
										<!-- <view class="address-space" v-if="item.find_type == 2">
											<view></view>
											
											<view class="flex align-item-start lh42">
												<view class="fs26 c999 mgr30">{{item.get_address.consignee}} {{item.get_address.mobile}}</view>
												<view class="flex-1 fs26 c999">
													{{item.get_address.stall || ''}}
												</view>
											</view>
											<view class="fs26 lh42">
												<text v-if="item.get_address.remark" class="tag lh42">{{item.get_address.remark}}</text>
												{{item.get_address.city_str}} {{ item.get_address.address}}
											</view>
											
										</view> -->

										<!--  -->
										<view class="address-space" v-if="item.get_address">
											<view class="mgb-20">{{nav==1?'':''}}</view>

											<view class="flex align-item-start lh42 mgb-20">
												<!-- <text v-if="item.get_address.remark" class="tag lh42 mgl-20">{{item.get_address.remark}}</text> -->
												<view class="fs26 c999 mgr30"> {{item.get_address.consignee}} {{item.get_address.mobile}} <text class="mgl-20">{{item.get_address.stall}}</text> </view>
												<!-- <view class="flex-1 fs26 c999">
													{{item.get_address.stall || ''}}
												</view> -->
											</view>
											<view class="fs26 lh42 text-666">
												{{item.get_address.city_str}} {{ item.get_address.address}}
											</view>
										</view>
									</view>
								</view>
							</view>
							<!--  -->
							<view class="flex flex-end order-handle">
								<!--  -->
								<!-- <view v-if="status == 2" class="flex find-status">
									<view :data-type='2' :data-id="item.id" @click='showForm'>{{nav==1?'':''}}</view>
									<view :data-type='1' :data-id="item.id" @click='showForm' class="ctheme warm-border">{{nav==1?'':''}}</view>
								</view> -->

								<view v-if="item.find_status == 1" class="flex find-status mgr-20" @click="receiptOrder(item.id)">
									<view></view>
								</view>
								
								<!-- <view v-if="item.user_id!=''" class="flex find-status mgr-20" @click="goChat(item)">
									<view></view>
								</view> -->
								<view class="cancat flr" v-if="item.user_id!=''">
									<image src="../../static/icon/concat.png"></image>
									<text></text>
									<view class="btn-1" @click.stop="goChat(item)"></view>
									<view class="btn-2" @click.stop="contact(item)"></view>
								</view>
								
							</view>
						</view>
					</block>

					<view v-if="isFullLoad" class="tc fs24 c999 bg-base lh60 b600 isFullLoad">
						
					</view>
					
					<view class="height200"></view>
				</view>
			</view>
		</view>
		<view v-if="formshow" class="pop-window" @touchmove="preventD">
			<!--  -->
			<view v-if="formtype =='1'" class="form-box-1">
				<text @click='close' class="iconfont icon-guanbi close"></text>
				<form @submit="formSubmit1" report-submit>
					<view class="fs34 b600 bdb lh120"></view>
					<view class="flex tl bdb lh80">
						<view class="mgr20">
							<text class="ctheme"></text>: </view>
						<input name="consignee" class="flex-1" confirm-hold="true" confirm-type="next" placeholder=''></input>
					</view>
					<view class="flex tl bdb lh80">
						<view class="mgr20">
							<text class="ctheme"></text>: </view>
						<input name="mobile" type='number' maxlength="11" class="flex-1" confirm-hold="true" confirm-type="next"
						 placeholder=''></input>
					</view>
					<view class="flex tl bdb lh80" bindtap='getMapAddress'>
						<view class="mgr20">
							<text class="ctheme"></text>: </view>
						<input name="address" v-model='address' maxlength="11" class="flex-1" confirm-hold="true" disabled='true'
						 confirm-type="next" placeholder=''></input>
					</view>
					<view class="flex tl bdb lh80">
						<view class="mgr20">
							<text class="ctheme"></text>: </view>
						<input name="name" v-model='name' maxlength="11" class="flex-1" confirm-hold="true" confirm-type="next"
						 placeholder=''></input>
					</view>
					<view class="flex tl bdb lh80">
						<view class="mgr20">
							<text class="room"></text>: </view>
						<input name="room" maxlength="11" class="flex-1" confirm-hold="true" confirm-type="next" placeholder=''></input>
					</view>
					<view class="flex tl bdb lh80">
						<view class="mgr20">
							<text class="room"></text>: </view>
						<input name="remark" maxlength="11" class="flex-1" confirm-hold="true" confirm-type="next" placeholder=''></input>
					</view>
					<view class="submit-box">
						<button formType="submit"></button>
					</view>
				</form>
			</view>
			<!--  -->
			<view v-if="formtype == '2'" class="form-box-2" @touchmove="preventD">
				<text @click='close' class="iconfont icon-guanbi close"></text>
				<form @submit="formSubmit2" report-submit>
					<view class="fs34 b600">{{nav==1?'':''}}</view>
					<view class="fs24 lh36">{{nav==1?'':''}}</view>
					<textarea class="tl reason bdb bdt" :placeholder="''+nav==1?'':''+''" name="remark" />
					<view class="submit-box">
                <button formType="submit"></button>
            </view>
        </form>
    </view>
    <!--  -->
    <view v-if="formtype == '3'" class="form-box-3">
        <text @click='close' class="iconfont icon-guanbi close pd20"></text>
        <form @submit="formSubmit3" report-submit>
            <view class="fs34 b600 bdb lh120"></view>
            <view class="flex flex-start bdb form-add-img">
                <view>
                    <text class="ctheme">*</text></view>
                <upload id="upload" />
            </view>
            <view class="flex tl bdb lh60">
                <view class="mgr20 lh80">
                    <text class="ctheme">*</text></view>
                <input class="flex-1" name="back_info" confirm-type="done" placeholder=''></input>
            </view>
            <view class="submit-box">
                <button formType="submit"></button>
            </view>
        </form>
    </view>
</view>

<view  v-if="showCon" class="modal-mask" @click="changeModalCancel">
    <view class="modal-dialog">
      <view class="modal-title"></view>
      <view class="modal-content">
        
      </view>
      <view class="modal-footer">
        <view class="btn-cancel" @click.stop="changeModalCancel"></view>
        <button open-type="openSetting" class="btn-confirm button-on-view" style="padding:0upx;" @click.stop="changeModalCancel"></button>
      </view>
    </view>
  </view>
	</view>
</template>

<script>
	import api  from '../../../utils/api.js';
	import util from '../../../utils/util.js';
	export default {
		data() {
			return {
				scrolLeft:1,
				searchValue:'',
				params: {
					task_type: 1,
					type: -1,
				},
			    page: 1,
			    status: 1,
				
			    nav:1,
				navSecend:0,
			    multiIndex: [7, 0, 8],
			    orderList:[],
			    isFullLoad:false,
			    address:'',
			    name:'',
				navTexts:{
					find:['','','','','','',''],
					fetch:['','','','','','']
				}
			};
		},
	
		onLoad(options) {
			
			uni.setNavigationBarTitle({
				title: ""
			})
		},
		onShow() {
			this.$data.nav = wx.getStorageSync('nav') || 1;
			this.$data.status = wx.getStorageSync('status') || 0;
			// this.$data.navSecend = wx.getStorageSync('status') || 0;
			
			
			if(this.$data.status == 0){
				this.$data.navSecend = 0; 
			}else if(this.$data.status == 1){
				this.$data.navSecend = 1; 
			}else if(this.$data.status == 2){
				this.$data.navSecend = 2; 
			}else if(this.$data.status == 3){
				this.$data.navSecend = 6; 
			}else if(this.$data.status == 4){
				this.$data.navSecend = 3; 
			}else if(this.$data.status == 5){
				this.$data.navSecend = 4; 
			}else if(this.$data.status == 6){
				this.$data.navSecend = 5; 
			}
			
			this.$data.orderList = [] 
			
			  // 
			this.getMyOrders();
			
		},
		mounted(){
			this.$data.nav = wx.getStorageSync('nav') || 1;
			this.$data.status = wx.getStorageSync('status') || 0;
			this.$data.navSecend = wx.getStorageSync('status') || 0;
			
			
			this.$data.orderList = [] 
			
			  // 
			this.getMyOrders();
		},
		methods: {
			
			// 
			doSearch(){
				if(this.$data.searchValue == ''){
					util.errorTips('');
					return false;
				}
				api.apiStaffFetchSearch({
					data:{
						keyword:this.$data.searchValue
					}
				}).then((res)=>{
					if(res.code == 200 || res.code == 0){
						if(res.data.length>0){
							this.$data.navSecend = 0;
							this.$data.scrolLeft = 0;
							this.$data.orderList = [];
							this.$data.orderList = res.data;
						}else{
							util.errorTips('')
						}
					}else{
						util.errorTips(res.msg)
					}
				}).catch((e)=>{util.errorTips(e.msg || e.message)})
			},
			
			// 
			contact(item){
				let data={
					id:item.id,
					type:1
				}
				api.apiPhoneStaff({
					method:'POST',
					data
				}).then((res)=>{
					
					if(res.code == 200 || res.code == 0){
						uni.makePhoneCall({
							phoneNumber: res.data
						})
					}else{
						util.errorTips(res.msg);
					}
				}).catch((res)=>{
					util.errorTips(res.msg)
				})
			},
			// 
			goChat(item){
				uni.navigateTo({
					url:'/pages/chat/chat?fromUserId='+uni.getStorageSync('userInfo').id+'&toUserId=' + item.user_id + '&name='
				})
			},
			// 
			receiptOrder(id){
				let _this = this;
				uni.showModal({
					title: '',
					content: '?',
					success: function (res) {
						if (res.confirm) {
							console.log('');
							api.staffFindGet({
								method:'POST',
								data:{
									id
								}
							}).then((res)=>{
								if(res.code == 200 || res.code == 0){
									// _this.$data.status = 2; // 2 
									  // 
									_this.$data.orderList = [];
									_this.getMyOrders();
									util.successTips("");
								}else{
									util.successTips(res.msg);
									_this.getMyOrders();
								}
							}).catch((res)=>{
								util.errorTips(res.msg || res.message);
								_this.getMyOrders();
							})
						} else if (res.cancel) {
							console.log('');
						}
					}
				});
			},
			// 
			  getMyOrders(){
				   let  status  = this.$data.status;
				  if(this.$data.nav == 1){
					   api.myOrderFindList({
						 data: {
							 page: this.$data.page,
							 status
						 }
					  }).then((res) => {
						  if(res.code == 200 || res.code == 0){
							  if(res.data.length <10) this.$data.isFullLoad = true;
							  this.$data.orderList = this.$data.orderList.concat(res.data);
						  }else{
							  // if(res.msg == "1"){
								 //   uni.navigateTo({
									// url:'/pages/login/login?from='+ uni.getStorageSync('userType')
								 //   })
							  // }
						  }
					  }).catch((res)=>{
						  util.errorTips(res.msg || res.message)
					  })
				  }else if(this.$data.nav == 2){
						
					   api.myOrderFetchList({
						 data: {
							 page: this.$data.page,
							 status
						 }
					  }).then((res) => {
						  if(res.code == 200 || res.code == 0){
							   if(res.data.length <10) this.$data.isFullLoad = true;
							  this.$data.orderList = this.$data.orderList.concat(res.data);
						  }
					  }).catch((res)=>{
						   util.errorTips(res.msg || res.message)
					  })
				  }
			  },
			  
			  // 
				getOrderData(e) {
					this.$data.nav = e.currentTarget.dataset.nav;
					uni.setStorageSync('nav',this.$data.nav);
					uni.setStorageSync('status', 1);
					this.$data.isFullLoad = false;
					this.$data.page = 1;
					this.$data.status = 0;
					this.$data.navSecend = 0;
					
					this.$data.orderList = [];
					
					this.getMyOrders()
				},
				// 
				getOrderTypeData(e) {
					this.$data.navSecend = e.currentTarget.dataset.type;
					
					if(this.$data.navSecend == 0){
						this.$data.status = 0; 
					}else if(this.$data.navSecend == 1){
						this.$data.status = 1; 
					}else if(this.$data.navSecend == 2){
						this.$data.status = 2; 
					}else if(this.$data.navSecend == 3){
						this.$data.status = 4; 
					}else if(this.$data.navSecend == 4){
						this.$data.status = 5; 
					}else if(this.$data.navSecend == 5){
						this.$data.status = 6; 
					}else if(this.$data.navSecend == 6){
						this.$data.status = 3; 
					}
					uni.setStorageSync('status', this.$data.status);
					this.$data.page = 1;
					this.$data.isFullLoad = false;
					this.$data.orderList = [];
					
					
					this.getMyOrders()
				},
				
				// 
				seeDetail(e) {
				  let index = e.currentTarget.dataset.index;
				  let id = e.currentTarget.dataset.id;
				  uni.navigateTo({
					url: "../index/findPages/findOrderDetail/findOrderDetail?index="+this.$data.nav+"&id="+id+"&status="+this.$data.status,
				  })
					
				},

				// 

				preview(e) {
					let index = e.currentTarget.dataset.index;
					let idx = e.currentTarget.dataset.idx;
				    let imgs = this.$data.orderList[index].desc_img;
					uni.previewImage({
						current: imgs[idx], // http
						urls: imgs // http
					})
				},
		},
		onPullDownRefresh: function () {
			this.$data.page++;
			this.getMyOrders();
			uni.stopPullDownRefresh()
		},
		onReachBottom: function () {
		  if (!this.$data.isFullLoad){
			this.$data.page++;
			this.getMyOrders();
		  }
		},
	}
</script>

